import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
            activeTab: 0,
            med: [],
            search: '',
            pres: [],
            user: this.$store.state.userInfo,
            page: 1,
            haveMore: true,
            showConfirm: false,
            showConfirm2: false,
            delPreId: null,
            delPreIdx: null,
            delMedId: null,
            delMedIdx: null,
            ListDetails: null,
            removeMedI: null,
            removeMedIdx: null,
            showConfirm3: false,
        }
    },
    watch: {
        activeTab(){
            this.page = 1
            this.haveMore = true
            this.pull()
        }
    },
    created(){
        if(this.$store.state.isUp){
            this.activeTab = this.$store.state.isUp
        }
        this.pull(false)
    },
    mounted(){
        this.pull(false)
        this.getMore()
    },
    beforeDestroy(){
        this.$store.dispatch('pushisUp',this.activeTab)
        window.onscroll = null
    },
    methods: {
        addTo(i){
            if(this.ListDetails.length == 5){
                return this.$toast('每张处方笺只能开5种药')
            }
            this.$store.dispatch('pushcMed', i)
            this.$router.push('/consumption?back=-2')
        },
        toAddPre(){
            this.$store.dispatch('pushupList', [])
            this.$store.dispatch('pushDiagnoses', null)
            this.$router.push('/usup-detail')
        },
        confirm(){
            this.$post('Prescription/Prescription/DelPrescriptionOftenUsed',{
                Id: this.delPreId
            }).then( o => {
                this.pres.splice(this.delPreIdx, 1)
            })
        },
        confirm2(){
            this.$post('Prescription/Medication/DelMedicationOftenUsed',{
                Id: this.delMedId
            }).then( o => {
                this.med.splice(this.delMedIdx, 1)
                if(this.med.length < 15){
                    this.page = 1
                    this.pull(false)
                }
            })
        },
        searchFor(){
            this.$store.dispatch('pushsearchText',this.search)
            if(this.$route.query.from == 'p'){
                this.$router.push(`/medicine-list?id=&from=ump`)
            }
            else if(this.$route.query.from == 'up'){
                this.$router.push(`/medicine-list?id=&from=upp`)
            }
            else{
                this.$router.push(`/medicine-list?id=&from=um`)
            }
        },
        pull(h){
            if(!this.activeTab && this.haveMore){
                this.$post('Prescription/Medication/GetMedicationOftenUsed',{
                    DoctorId: this.user.Data.User.DoctorId,
                    OrgId: this.user.Data.User.OrgId,
                    MedicationName: this.search,
                    PageIndex: this.page,
                    PageSize: 15
                }).then(o => {
                    this.ListDetails = this.$store.state.upList
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        const list = this.$store.state.upList
                        o.ReturnData.forEach(v => {
                            list.forEach(a => {
                                if(v.Name === a.Name && v.Brand === a.Brand && v.Manufacturer == a.Manufacturer){
                                    v.state = 'onUp'
                                }
                            })
                            this.med.push(v)
                        })
                        
                    }
                    else {
                        const list = this.$store.state.upList
                        o.ReturnData.forEach(v => {
                            list.forEach(a => {
                                if(v.Name === a.Name && v.Brand === a.Brand && v.Manufacturer == a.Manufacturer){
                                    v.state = 'onUp'
                                }
                            })
                        })
                        this.med = o.ReturnData
                    }
                })
            }
            else{
                this.$post('Prescription/Prescription/GetAllPrescriptionOftenUsed',{
                    DoctorId: this.user.Data.User.DoctorId,
                    Name: this.search,
                    OrgId: this.user.Data.User.OrgId,
                    PageIndex: 1,
                    PageSize: 15
                }).then(o => {
                    this.ListDetails = this.$store.state.upList
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.pres.push(v)
                        })
                    }
                    else {
                        this.pres = o.ReturnData
                    }
                }) 
            }
        },
        pop(id,idx){
            this.delMedId = id
            this.delMedIdx = idx
            this.showConfirm2 = true
        },
        remove(i,idx){
            this.removeMedI = i
            this.removeMedIdx = idx
            this.showConfirm3 = true
        },
        confirm3(){
            this.ListDetails.forEach((v,k) => {
                if(v.Id == this.removeMedI.Id){
                    this.ListDetails.splice(k,1)
                }
            })
            this.med[this.removeMedIdx].state = null
        },
        popPre(id,idx){
            this.delPreId = id
            this.delPreIdx = idx
            this.showConfirm = true
        },
        toAdd(){
            const from = this.$route.query.from || 'um'
            this.$router.push(`/all-medicine?from=${from}`)
        },
        addToUp(i){
            this.$store.dispatch('pushcMed', i)
            this.$router.push('/consumption?back=-2')
        },
        getMore(){
            const el = this.$refs.el
            let timer = null

            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        Modify(i){
            if(this.$route.query.from === 'p'){
                return
            }
            else if(!i.IsActive){
                return
            }
            else{
                this.$router.push(`/usup-detail?id=${i.Id}`)
            }
        },
        chooseForPre(i){
            i.ListDetails.forEach((v,k) => {
                if(v.Invalid == 1){
                    i.ListDetails.splice(k,1)
                }
            })
            
            this.$store.dispatch('pushupList', i.ListDetails)
            this.$store.dispatch('pushDiagnoses2', i.Diagnoses)
            this.$router.back()
        }
    }
}



